import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useInspectorTypesList() {
  const inspectorTypesList = ref([]);
  const fetchInspectorTypesList = async () => {
    const { data } = await InfoService.fetchInspectorTypesList();
    inspectorTypesList.value = data;
  };
  onMounted(async () => {
    await fetchInspectorTypesList();
  });

  return {
    inspectorTypesList,
  };
}
