import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useBringingReason(regionModel) {
  const bringingReasonList = ref([]);
  const fetchBringingReasonList = async () => {
    const {
      data: { reason_bringing_child },
    } = await InfoService.fetchBringingReasonList();
    bringingReasonList.value = reason_bringing_child;
  };
  onMounted(async () => {
    await fetchBringingReasonList();
  });

  return {
    bringingReasonList,
  };
}
