<template>
  <form @submit="onSubmit">
    <section class="grid grid-cols-2 gap-4 2xl:w-[60%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="inspector_type"> Markazga olib kelgan xodim </label>
        <SelectBox id="inspector_type" v-model="inspector_type" :options="inspectorTypesList" show-by="text" track-by="id" placeholder="Xodimni tanlang" />
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="pinflInspector"> JSHSHIR </label>
        <div class="flex items-center gap-2">
          <input v-validate-cyrillic id="pinflInspector" v-model="pinfl" v-maska data-maska="##############" class="base_input w-full" placeholder="JSHSHIR ni kiriting" name="pinfl" />
          <button class="self-start px-[12px] py-[6px] bg-primary h-[45px] text-white hover:opacity-40 rounded" @click.prevent="getProfilacticInspectors">Qidirish</button>
        </div>
        <span class="error_message">{{ pinflError }}</span>
      </div>
      <div class="form_wrapper my-3" v-if="allowCertNumber">
        <label class="input_label" for="certificate_numberInspector"> Guvohnoma raqami </label>
        <input v-validate-cyrillic id="certificate_numberInspector" v-model="certificate_number" class="base_input" placeholder="Guvohnoma raqami" name="certificate_number" type="text" />
        <span class="error_message">{{ certificate_numberError }}</span>
      </div>
      <!-- <div class="form_wrapper my-3">
        <label class="input_label"> JSHSHIR bo'yicha inspektorni qidirish </label>
        <button class="self-start w-[150px] px-[12px] py-[6px] bg-primary h-[45px] text-white hover:opacity-40 rounded" @click.prevent="getProfilacticInspectors">Qidirish</button>
      </div> -->
    </section>
    <section class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="last_nameInspector"> Familiyasi </label>
        <input v-validate-cyrillic id="last_nameInspector" :disabled="disabled" v-model="last_name" class="base_input" placeholder="Familiyasini kiriting" name="last_name" type="text" />
        <span class="error_message">{{ last_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="first_nameInspector"> Ismi </label>
        <input v-validate-cyrillic id="first_nameInspector" :disabled="disabled" v-model="first_name" class="base_input" placeholder="Ismini kiriting" name="first_name" type="text" />
        <span class="error_message">{{ first_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="father_nameInspector"> Sharifi </label>
        <input v-validate-cyrillic id="father_nameInspector" :disabled="disabled" v-model="father_name" class="base_input" placeholder="Sharifini kiriting" name="father_name" type="text" />
        <span class="error_message">{{ father_nameError }}</span>
      </div>
      <h1 class="text-xl my-2 col-span-2 xl:col-span-3">Xizmat olib boruvchi hududi</h1>
      <div class="form_wrapper my-3">
        <label class="input_label" for="region"> Viloyat </label>
        <SelectBox id="region" v-model="region" :options="regionsList" show-by="name" track-by="id" placeholder="Viloyatni tanlang" @input="fetchDistrictsByRegionId" />
        <span class="error_message">{{ regionError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="district"> Tuman </label>
        <SelectBox id="district" v-model="district" :options="districtsList" show-by="name" track-by="id" placeholder="Tumanni tanlang" />
        <span class="error_message">{{ districtError }}</span>
      </div>
    </section>
    <button class="rounded-md py-2 px-8 h-12 bg-primary text-white" type="submit">Qo'shish</button>
  </form>
  <section>
    <Loading v-if="isLoading" v-model:active="isLoading" :can-cancel="false" :is-full-page="true" color="#4785FE" loader="dots" />
    <ProfilacticInspectorCard v-for="insp in existProfilacticInspectorsFromServer" :key="insp.id" :inspector="insp" @card-clicked="setSelectedInspector" />
  </section>
  <section v-if="inspector" class="my-4 shadow-md rounded-md p-3 w-[700px]">
    <h1 class="text-xl my-2">Bolani Markazga olib kelgan xodim haqida ma'lumot</h1>
    <div class="flex justify-between px-4 py-2 rounded-md">
      <p class="text-2xl">{{ inspector.first_name }} {{ inspector.last_name }} {{ inspector.father_name }} - {{ inspector.pinfl }}</p>
      <button @click="deleteSelectedInspector()">
        <Icon icon="mdi:close" class="hover:text-red-600 text-gray-600" width="20" height="20" />
      </button>
    </div>
  </section>
</template>

<script setup>
import {vMaska} from 'maska';
import SelectBox from './Common/SelectBox';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import {computed, defineEmits, defineProps, ref, toRefs, watch} from 'vue';
import useDistrict from '../composables/selectbox-list/useDistrict';
import useRegion from '../composables/selectbox-list/useRegion';
import {Icon} from '@iconify/vue';
import ProfilacticInspectorCard from './ProfilacticInspectorCard';
import useInspectorTypesList from '../composables/selectbox-list/useInspectorTypesList';
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import {maxValidatorMessage, minValidatorMessage, requiredValidatorMessage} from '../utils/validatorMessages';
import SearchService from '../services/search.service';
import useAlert from '../composables/useAlert';

const {regionsList} = useRegion();
const {alertIt} = useAlert();
const {districtsList, fetchDistrictsByRegionId} = useDistrict();
const INSPECTOR_TYPE = 1;

const disabled = ref(false);

const props = defineProps({
  inspector: {
    type: Object,
  },
});

const {inspector} = toRefs(props);
const emit = defineEmits(['card-clicked', 'inspectors-selected', 'update:inspector']);

const canBeFilled = computed(() => {
  return inspector_type.value !== INSPECTOR_TYPE;
});
const allowCertNumber = computed(() => {
  return inspector_type.value === INSPECTOR_TYPE;
});

const {inspectorTypesList} = useInspectorTypesList();

const inspector_type = ref(undefined);
const isLoading = ref(false);
const existProfilacticInspectorsFromServer = ref([]);

const allowedToSearchInspector = computed(() => {
  return inspector_type.value === INSPECTOR_TYPE && pinfl.value.length === 14;
});

const {handleSubmit, resetForm} = useForm();

function onInvalidSubmit({errors}) {
  console.log(errors);
}

const onSubmit = handleSubmit(async (values) => {
  setSelectedInspector(values);
  resetForm();
}, onInvalidSubmit);

function setSelectedInspector(insp) {
  let inspObj = {
    ...insp,
    inspector_type: inspector_type.value,
  };
  if (inspector_type.value !== INSPECTOR_TYPE) {
    inspObj.id = null;
  }
  existProfilacticInspectorsFromServer.value = [];
  emit('update:inspector', inspObj);
  resetForm();
}

function deleteSelectedInspector() {
  emit('update:inspector', null);
}

async function getProfilacticInspectors() {
  isLoading.value = true;
  const params = {
    pinfl: pinfl.value,
  };
  try {
    const {data} = await SearchService.searchInspectorsInCenters(params);
    console.log(data);
    if (data.AnswereId !== 1) {
      alertIt(`Kiritilgan JSHSHIR bo'yicha profilaktik inspectorlar topilmadi`, 'info');
    } else {
      const result = data.Data;
      console.log(result);
      first_name.value = result.first_name_latin;
      last_name.value = result.last_name_latin;
      father_name.value = result.patronymic_name_latin;
      disabled.value = true;
    }
  } catch (e) {
    alertIt(`Kiritilgan JSHSHIR bo'yicha profilaktik inspectorlar topilmadi`, 'info');
  } finally {
    isLoading.value = false;
  }
}

const dynamicValidatorForAddingInspector = computed(() => {
  if (inspector_type.value !== INSPECTOR_TYPE) {
    return yup.string().required(requiredValidatorMessage);
  } else return yup.string();
});
const dynamicValidatorCertNumber = computed(() => {
  if (inspector_type.value === INSPECTOR_TYPE) {
    return yup.string().required(requiredValidatorMessage);
  } else return yup.string().notRequired();
});

const {value: first_name, errorMessage: first_nameError} = useField('first_name', dynamicValidatorForAddingInspector, {initialValue: ''});
const {value: certificate_number, errorMessage: certificate_numberError} = useField('certificate_number', dynamicValidatorCertNumber, {initialValue: ''});
const {value: last_name, errorMessage: last_nameError} = useField('last_name', dynamicValidatorForAddingInspector, {initialValue: ''});
const {value: father_name, errorMessage: father_nameError} = useField('father_name', dynamicValidatorForAddingInspector, {initialValue: ''});

const dynamicValidatorForInspectorsPinfl = computed(() => {
  return canBeFilled.value
    ? yup.string().required(requiredValidatorMessage).min(14, minValidatorMessage).max(14, maxValidatorMessage)
    : yup.string().min(14, minValidatorMessage).max(14, maxValidatorMessage);
});

const {value: pinfl, errorMessage: pinflError} = useField('pinfl', dynamicValidatorForInspectorsPinfl, {initialValue: ''});

watch(
  () => pinfl.value,
  async (newPinfl) => {
    if (inspector_type.value === INSPECTOR_TYPE && newPinfl.length === 14) {
      // await getProfilacticInspectors()
    }
  }
);

const {value: district, errorMessage: districtError} = useField('district', yup.string().required(requiredValidatorMessage), {initialValue: ''});

const {value: region, errorMessage: regionError} = useField('region', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});
</script>

<style scoped></style>
