import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useDeterminedLocations() {
  const determinedLocations = ref([]);
  const fetchDeterminedLocations = async () => {
    const { data } = await InfoService.fetchDeterminedLocations();
    determinedLocations.value = data;
  };
  onMounted(async () => {
    await fetchDeterminedLocations();
  });

  return {
    determinedLocations,
  };
}
