import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useArrivedReasons() {
  const arrivedReasons = ref([]);
  const fetchArrivedReasons = async () => {
    const { data } = await InfoService.fetchArrivedReasons();
    arrivedReasons.value = data;
  };
  onMounted(async () => {
    await fetchArrivedReasons();
  });

  return {
    arrivedReasons,
  };
}
