<template>
  <section class="bg-white rounded-md p-3">
    <PageTitle> Markazga qabul qilish </PageTitle>
    <section>
      <TableTeenagersForSelectWrapper v-model:selected-juvenile="selectedJuvenile" :update-juveniles-list="updateJuvenilesList" :fetch-juveniles-list-handler="fetchJuvenilesList" />
      <span id="selectedJuvenileError" class="error_message">{{ selectedJuvenileError }}</span>
    </section>
    <section v-if="selectedJuvenile">
      <h1 class="text-2xl my-4">Tanlangan bolani shaxsiy ma`lumotlari</h1>
      <div class="flex">
        <PassportCard :passport-info="selectedJuvenile" @canceled="setSelectedJuvenile(null)" />
      </div>
    </section>
    <AddJuvenileToCenterForm
      v-if="selectedJuvenile"
      :initial-fields="{arrived_count: selectedJuvenile?.accepted_center_number}"
      :selectedJuvenile="selectedJuvenile"
      :clear-form-mode="clearForm"
      @submit="onSubmit"
    />
  </section>
  <ModalComponent modalClass="!w-auto" ref="modalComponentRef" modal-position="center" :close-on-click-away="false" :hide-close-button="true">
    <template #slotBody>
      <div class="flex flex-col justify-between gap-[44px]">
        <div class="text-3xl flex items-center justify-center">
          <h1>Bola markazga muvaffaqiyatli qabul qilindi</h1>
          <Icon icon="mdi:check-circle" class="mx-2 text-primary w-[34px] h-[34px]" />
        </div>
        <div class="flex justify-center">
          <button class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80" @click="modalComponentRef.closeModal">Davom etish</button>
          <button class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80" @click="$router.push('/')">Bosh sahifa</button>
        </div>
      </div>
    </template>
  </ModalComponent>
</template>

<script setup>
import ModalComponent from '../components/Common/Modal/ModalComponent';
import {Icon} from '@iconify/vue';
import AddJuvenileToCenterForm from '../components/AddJuvenileToCenterForm';
import TableTeenagersForSelectWrapper from '../components/Tables/TableTeenagersForSelectWrapper';
import PassportCard from '../components/PassportCard';
import {ref, watch} from 'vue';
import useAlert from '../composables/useAlert';
import useScrollToError from '../composables/useScrollToError';
import PageTitle from '../components/LayoutItems/PageTitle';
import JuvenilesService from '../services/juveniles.service';
import useErrorAlert from '../composables/useErrorAlert';
import useClearForm from '../composables/useClearForm';

const {fetchJuvenilesList, acceptJuvenileToCenter} = JuvenilesService;
const {alertIt} = useAlert();
const {alertThisError} = useErrorAlert();
const {clearForm, setClearForm} = useClearForm();
const modalComponentRef = ref();
const {scrollToError} = useScrollToError();
const selectedJuvenile = ref(null);
const updateJuvenilesList = ref(false);
const selectedJuvenileError = ref('');

async function onSubmit(formData) {
  if (!selectedJuvenile.value) {
    selectedJuvenileError.value = 'Bola tanlanishi shart!';
    scrollToError('selectedJuvenileError');
    return null;
  }
  try {
    const {
      data: {message},
    } = await acceptJuvenileToCenter(selectedJuvenile.value.id, formData);
    await alertIt(message, 'success');
    clearAllInputData();
    modalComponentRef.value.openModal();
  } catch (e) {
    alertThisError(e);
  }
}

watch(
  () => selectedJuvenile.value,
  (val) => {
    if (val && selectedJuvenileError.value) {
      selectedJuvenileError.value = '';
    }
  }
);

function setSelectedJuvenile(juvenile) {
  selectedJuvenile.value = juvenile;
}

function clearAllInputData() {
  setClearForm(true);
  selectedJuvenile.value = null;
  updateJuvenilesList.value = true;
}
</script>

<style scoped></style>
