import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useMedicalListTypes() {
  const medicalListTypes = ref([]);
  const fetchMedicalListTypes = async () => {
    const { data } = await InfoService.fetchMedicalListTypes();
    medicalListTypes.value = data;
  };
  onMounted(async () => {
    await fetchMedicalListTypes();
  });

  return {
    medicalListTypes,
  };
}
