import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useHaveBeenInItmTypes() {
  const haveBeenInItmTypes = ref([]);
  const fetchHaveBeenInItmTypes = async () => {
    const { data } = await InfoService.fetchHaveBeenInItmTypes();
    haveBeenInItmTypes.value = data;
  };
  onMounted(async () => {
    await fetchHaveBeenInItmTypes();
  });

  return {
    haveBeenInItmTypes,
  };
}
