import axios from './api/axios';

export default {
  async getPsychiatristWithPNFL(params) {
    return await axios.get('api/info/is_registered_psychiatrist_dispensary_by_pinfl/', {params});
  },
  async getPsychiatristWithPassport(params) {
    return await axios.get('api/info/is_registered_psychiatrist_dispensary_by_birth_doc/', {params});
  },
  async getNarkoWithPNFL(params) {
    return await axios.get('api/info/is_registered_narko_dispensary_by_pinfl/', {params});
  },
  async getNarkoWithPassport(params) {
    return await axios.get('api/info/is_registered_narko_dispensary_by_birth_doc/', {params});
  },
};
