<template>
  <form @submit="onSubmit">
    <!-- {{ selectedJuvenile }} -->
    <section>
      <h3 class="my-4 text-2xl">Qo'shimcha ma'lumotlar</h3>
      <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label class="input_label" for="bringing_reason"> Bolani olib kelish sababi turi </label>
          <SelectBox
            id="bringing_reason"
            v-model="bringing_reason"
            :options="bringingReasonList"
            show-by="title"
            track-by="id"
            placeholder="Sababi turini tanlang"
            @input="fetchSubBringingReasonList"
          />
          <span class="error_message">{{ bringing_reasonError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="sub_reason_bringing_child"> Bolani olib kelish holati </label>
          <SelectBox id="sub_reason_bringing_child" v-model="sub_reason_bringing_child" :options="subBringingReasonList" show-by="title" track-by="id" placeholder="Holatini tanlang" />
          <span class="error_message">{{ sub_reason_bringing_childError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="determined_location"> Holat aniqlangan joyi </label>
          <SelectBox id="determined_location" v-model="determined_location" :options="determinedLocations" show-by="text" track-by="id" placeholder="Holat aniqlangan joyni tanlang" />
          <span class="error_message">{{ determined_locationError }}</span>
        </div>
        <div v-if="allowedToSelectForeignCountry" class="form_wrapper my-3">
          <label class="input_label" for="passport_type"> Horijiy davlat </label>
          <SelectBox id="foreign_country" v-model="foreign_country" :options="countriesList" show-by="name" track-by="id" placeholder="Horijiy davlatni tanlang" />
          <span class="error_message">{{ foreign_countryError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label"> Olib kelingan sana </label>
          <DateComponent v-model="arrived_date" placeholder="Sanani tanlang" />
          <span class="error_message">{{ arrived_dateError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="arrived_reason"> Olib kelinishiga asos </label>
          <SelectBox id="arrived_reason" v-model="arrived_reason" :options="arrivedReasons" show-by="text" track-by="id" placeholder="Asosni tanlang" />
          <span class="error_message">{{ arrived_reasonError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label id="arrived_reason_file" class="input_label"> Asos </label>
          <FileInput :id="'arrived_reason_file1'" v-model:files="arrived_reason_file" :initial-files="initialArrivedReasonFile" :multiple-files="false" class="w-full">
            <template #icon>
              <div class="flex justify-between shadow rounded w-full">
                <p class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">Hujjatni yuklang</p>
                <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer"> YUKLASH </span>
              </div>
            </template>
          </FileInput>
          <span class="error_message">{{ arrived_reason_fileError }}</span>
        </div>
        <div class="form_wrapper my-3">
          <label class="input_label"> Profilaktik hisobda turadi </label>
          <ButtonGroup id="gender" v-model:selected-item="prophylactic_list" :titles="btnGroupYesNo" />
          <span class="error_message">{{ prophylactic_listError }}</span>
        </div>
        <div></div>
        <div></div>
        <div class="form_wrapper my-3">
          <label class="input_label" for="arrived_count"> Markazga nechanchi marta kelishi </label>
          <!-- {{ arrived_count + 1 }} -->
          <input v-model="arrived_count_showed" class="base_input" placeholder="Markazga nechanchi marta kelishi" name="arrived_count" disabled />
          <!-- <SelectBox id="arrived_reason" v-model="arrived_count" :options="[1, 2, 3]" placeholder="Markazda nechi marta bolgani" /> -->
        </div>
      </div>
      <div class="grid grid-cols-2 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label class="input_label"> Tibbiyot D ro‘yxatda turadimi </label>
          <ButtonGroup :disabled="is_in_medical_disabled" v-model:selected-item="is_in_medical_d_list" :titles="btnGroupYesNo" />
          <span class="error_message">{{ is_in_medical_d_listError }}</span>
        </div>
        <div v-if="is_in_medical_d_list" class="form_wrapper my-3">
          <label class="input_label" for="medical_list"> D Ro'yxat </label>
          <SelectBox
            id="medical_list"
            :disabled="medicalSelect.disabled"
            v-model="medical_list"
            :is-multiselect="true"
            :options="medicalListTypes"
            show-by="title"
            track-by="id"
            placeholder="D Ro'yxat turini tanlang"
          />
          <span class="error_message">{{ medical_listError }}</span>
        </div>
      </div>
      <div class="grid grid-cols-2 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label id="is_in_rotm" class="input_label">Avval RO‘TMda bo‘lganmi</label>
          <ButtonGroup v-model:selected-item="is_in_rotm" :titles="btnGroupYesNo" />
          <span class="error_message">{{ is_in_rotmError }}</span>
        </div>
        <div v-if="is_in_rotm" class="form_wrapper my-3">
          <label class="input_label" for="have_been_in_rotm_reason"> RO‘TMdan qanday ketgan </label>
          <SelectBox id="have_been_in_rotm_reason" v-model="have_been_in_rotm_reason" :options="haveBeenInRotmTypes" show-by="text" track-by="id" placeholder="RO‘TMdan qanday ketgan" />
          <span class="error_message">{{ have_been_in_rotm_reasonError }}</span>
        </div>
      </div>
      <div class="grid grid-cols-2 gap-4 2xl:w-[60%]">
        <div class="form_wrapper my-3">
          <label id="is_in_itm" class="input_label">Avval ITMda bo‘lganmi</label>
          <ButtonGroup v-model:selected-item="is_in_itm" :titles="btnGroupYesNo" />
          <span class="error_message">{{ is_in_itmError }}</span>
        </div>
        <div v-if="is_in_itm" class="form_wrapper my-3">
          <label class="input_label" for="have_been_in_itm_reason"> ITMdan qanday ketgan </label>
          <SelectBox id="have_been_in_itm_reason" v-model="have_been_in_itm_reason" :options="haveBeenInItmTypes" show-by="text" track-by="id" placeholder="ITMdan qanday ketgan" />
          <span class="error_message">{{ have_been_in_itm_reasonError }}</span>
        </div>
      </div>
    </section>
    <section>
      <h3 class="my-4 text-2xl">Yig‘ma jild haqida ma'lumot</h3>
      <AddProfilacticInspector v-model:inspector="prophylactic_inspector" />
      <span class="error_message">{{ prophylactic_inspectorError }}</span>
      <div class="grid grid-cols-2 gap-4 2xl:w-[40%]">
        <div class="form_wrapper my-3">
          <label class="input_label"> Yig‘ma jild to‘ldirilgan sana va vaqt </label>
          <div class="base_input">
            {{ dayjs(filled_date).format('DD.MM.YYYY , HH:mm') }}
          </div>
          <span class="error_message">{{ filled_dateError }}</span>
        </div>
      </div>
    </section>
    <section>
      <button v-if="isEditMode" class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Tahrirlashni tugatish</button>
      <button v-else class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Saqlash</button>
    </section>
  </form>
</template>

<script setup>
import SelectBox from './Common/SelectBox';
import useMedicalListTypes from '../composables/selectbox-list/useMedicalListTypes';
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import {computed, defineEmits, defineProps, onMounted, onUnmounted, ref, toRefs, watch} from 'vue';
import ButtonGroup from './Common/ButtonGroup';
import AddProfilacticInspector from './AddProfilacticInspectorForm';
import useDeterminedLocations from '../composables/selectbox-list/useDeterminedLocations';
import useArrivedReasons from '../composables/selectbox-list/useArrivedReasons';
import FileInput from './Common/InputFile/FileInput';
import dayjs from 'dayjs';
import useScrollToError from '../composables/useScrollToError';
import useHaveBeenInRotmTypes from '../composables/selectbox-list/useHaveBeenInRotmTypes';
import useHaveBeenInItmTypes from '../composables/selectbox-list/useHaveBeenInItmTypes';
import {btnGroupYesNo} from '../utils/buttonGroupTitles';
import {requiredValidatorMessage, workerIsRequiredValidatorMessage} from '../utils/validatorMessages';
import useCountries from '../composables/selectbox-list/useCountries';
import useBringingReason from '../composables/selectbox-list/useBringingReason';
import useSubBringingReason from '../composables/selectbox-list/useSubBringingReason';
import useErrorAlert from '../composables/useErrorAlert';
import DateComponent from './Common/DateComponent';
import dispancerService from '../services/dispancer.service';

const {getNarkoWithPNFL, getNarkoWithPassport, getPsychiatristWithPNFL, getPsychiatristWithPassport} = dispancerService;

const BIOMETRIK_PASSPORT_TYPE = 1;
const ID_CARD_TYPE = 2;
const BIRTH_CERTIFICATE = 3;
const KINDER_PASSPORT = 4;
const PSYCHALOGY = 1;
const NARCOLOGY = 1;
const SKINDISPANCER = 1;
const RAK = 1;

const medicalSelect = ref({disabled: false});
const is_in_medical_disabled = ref(false);

const emit = defineEmits(['submit']);
const props = defineProps({
  selectedJuvenile: {
    type: Object,
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
  initialFields: {
    type: Object,
    default: function () {
      return {};
    },
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
});
const {isEditMode, initialFields, clearFormMode, selectedJuvenile} = toRefs(props);

const allowPNFLType = computed(
  () =>
    selectedJuvenile.value?.passport_documents?.passport_type == BIOMETRIK_PASSPORT_TYPE ||
    selectedJuvenile.value?.passport_documents?.passport_type == ID_CARD_TYPE ||
    selectedJuvenile.value?.passport_documents?.passport_type == KINDER_PASSPORT
);
const allowBirthType = computed(() => selectedJuvenile.value?.passport_documents?.passport_type == BIRTH_CERTIFICATE);

const {scrollToError} = useScrollToError();
const {determinedLocations} = useDeterminedLocations();
const {medicalListTypes} = useMedicalListTypes();
const {arrivedReasons} = useArrivedReasons();
const {countriesList} = useCountries();
const {haveBeenInRotmTypes} = useHaveBeenInRotmTypes();
const {haveBeenInItmTypes} = useHaveBeenInItmTypes();
const {bringingReasonList} = useBringingReason();
const {fetchSubBringingReasonList, subBringingReasonList} = useSubBringingReason();
const {alertThisError} = useErrorAlert();
const initialArrivedReasonFile = ref([]);
const {handleSubmit, isSubmitting, resetForm} = useForm();

(async () => {
  if (allowPNFLType.value) {
    const isNarko = await getNarkoWithPNFL({
      pinfl: selectedJuvenile.value?.passport_documents?.pinfl,
    });
    const isPsych = await getPsychiatristWithPNFL({
      pinfl: selectedJuvenile.value?.passport_documents?.pinfl,
    });
    if (!!isNarko.data?.data || !!isPsych.data?.data) {
      is_in_medical_d_list.value = true;
      is_in_medical_disabled.value = true;
      medicalSelect.value.disabled = true;
      medical_list.value = [];
      !isPsych.data?.data
        ? (medical_list.value = [...medical_list.value, medicalListTypes.value.find((n) => n.extra_id === PSYCHALOGY)])
        : (medical_list.value = [...medical_list.value, medicalListTypes.value.find((n) => n.extra_id === NARCOLOGY)]);
    }
  } else if (allowBirthType.value) {
    const isNarko = await getNarkoWithPassport({
      serial: selectedJuvenile.value?.passport_documents?.passport_seria.replace(selectedJuvenile.value?.passport_documents?.passport_seria.replace(/[^0-9]+/g, ''), ''),
      number: selectedJuvenile.value?.passport_documents?.passport_seria.replace(/[^0-9]+/g, ''),
      birthDate: selectedJuvenile.value?.birth_date,
    });
    const isPsych = getPsychiatristWithPassport({
      serial: selectedJuvenile.value?.passport_documents?.passport_seria.replace(selectedJuvenile.value?.passport_documents?.passport_seria.replace(/[^0-9]+/g, ''), ''),
      number: selectedJuvenile.value?.passport_documents?.passport_seria.replace(/[^0-9]+/g, ''),
      birthDate: selectedJuvenile.value?.birth_date,
    });
    if (!!isNarko.data?.data || !!isPsych.data?.data) {
      is_in_medical_d_list.value = true;
      is_in_medical_disabled.value = true;
      medicalSelect.value.disabled = true;
      medical_list.value = [];
      !isPsych.data?.data
        ? (medical_list.value = [...medical_list.value, medicalListTypes.value.find((n) => n.extra_id === PSYCHALOGY)])
        : (medical_list.value = [...medical_list.value, medicalListTypes.value.find((n) => n.extra_id === NARCOLOGY)]);
    }
  }
})();

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

const FOREIGN_LOCATION_TYPE = 5;

const allowedToSelectForeignCountry = computed(() => {
  return determined_location.value === FOREIGN_LOCATION_TYPE;
});

onMounted(() => {
  setInterval(setFilledDate, 60000);
});
onUnmounted(() => {
  clearInterval(setFilledDate);
});

watch(
  () => initialFields.value,
  () => {
    if (Object.keys(initialFields.value).length) {
      initializeFields(initialFields.value);
    }
  }
);

function setFilledDate() {
  filled_date.value = dayjs();
}

function onInvalidSubmit({errors}) {
  console.log(Object.keys(errors));
  scrollToError(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(async ({is_in_rotm, is_in_itm, is_in_medical_d_list, ...args}) => {
  try {
    console.log(is_in_rotm, is_in_itm, is_in_medical_d_list);
    const formData = new FormData();
    Object.entries(args).map(([key, val]) => {
      if (val || key === 'prophylactic_list') {
        if (key === 'prophylactic_inspector' || key === 'medical_list') {
          formData.append(key, JSON.stringify(val, null, 1));
        } else if (key === 'arrived_date') {
          formData.append(key, dayjs(val).toISOString());
        } else {
          formData.append(key, val);
        }
      }
    });
    emit('submit', formData);
  } catch (e) {
    console.log(e);
    alertThisError(e);
  }
}, onInvalidSubmit);

function initializeFields(fields) {
  // is_in_rotm.value = fields.is_in_rotm
  // is_in_itm.value = fields.is_in_itm
  // determined_location.value = fields.determined_location
  // arrived_date.value = fields.arrived_date
  // arrived_reason.value = fields.arrived_reason
  // console.log(fields);
  fields.accepted_center_number ? (arrived_count.value = fields.accepted_center_number) : null;
  if (fields.arrived_reason_file) {
    initialArrivedReasonFile.value = [fields.arrived_reason_file];
  }
  if (fields?.medical_list?.length) {
    medical_list.value = fields.medical_list;
    is_in_medical_d_list.value = true;
  } else {
    is_in_medical_d_list.value = false;
  }
  if (fields.have_been_in_itm_reason) {
    have_been_in_itm_reason.value = fields.have_been_in_itm_reason;
    is_in_itm.value = true;
  } else {
    is_in_itm.value = false;
  }
  if (fields.have_been_in_rotm_reason) {
    have_been_in_rotm_reason.value = fields.have_been_in_rotm_reason;
    is_in_rotm.value = true;
  } else {
    is_in_rotm.value = false;
  }
}

const arrived_count = ref(0);

const arrived_count_showed = ref(arrived_count.value + 1);
const {value: is_in_itm, errorMessage: is_in_itmError} = useField('is_in_itm', yup.mixed().required(requiredValidatorMessage));

const dynamicValidationForHaveBeenInItm = computed(() => {
  if (is_in_itm.value) {
    return yup.number().required(requiredValidatorMessage);
  } else {
    return yup.number().notRequired();
  }
});
const {value: have_been_in_itm_reason, errorMessage: have_been_in_itm_reasonError} = useField('have_been_in_itm_reason', dynamicValidationForHaveBeenInItm);

watch(
  () => is_in_itm.value,
  (val) => {
    if (!val) {
      have_been_in_itm_reason.value = undefined;
    }
  }
);

const {value: is_in_rotm, errorMessage: is_in_rotmError} = useField('is_in_rotm', yup.mixed().required(requiredValidatorMessage));

const dynamicValidationForHaveBeenInRotm = computed(() => {
  if (is_in_rotm.value) {
    return yup.number().required(requiredValidatorMessage);
  } else {
    return yup.mixed().notRequired();
  }
});

const {value: have_been_in_rotm_reason, errorMessage: have_been_in_rotm_reasonError} = useField('have_been_in_rotm_reason', dynamicValidationForHaveBeenInRotm);

watch(
  () => is_in_rotm.value,
  (val) => {
    if (!val) {
      have_been_in_rotm_reason.value = undefined;
    }
  }
);

const {value: is_in_medical_d_list, errorMessage: is_in_medical_d_listError} = useField('is_in_medical_d_list', yup.mixed().required(requiredValidatorMessage));

watch(
  () => is_in_medical_d_list.value,
  (val) => {
    if (!val) {
      medical_list.value = [];
    }
  }
);

const dynamicValidatorForMedicalDList = computed(() => {
  if (is_in_medical_d_list.value) {
    return yup.array().nullable().required(requiredValidatorMessage);
  } else return yup.array().nullable().notRequired();
});
const {value: medical_list, errorMessage: medical_listError} = useField('medical_list', dynamicValidatorForMedicalDList);

// watch(()=>medical_list.value,(value => console.log(value)))

const {value: prophylactic_list, errorMessage: prophylactic_listError} = useField('prophylactic_list', yup.mixed().required(requiredValidatorMessage), {initialValue: false});
const {value: arrived_reason, errorMessage: arrived_reasonError} = useField('arrived_reason', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: bringing_reason, errorMessage: bringing_reasonError} = useField('bringing_reason', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

// watch(()=>bringing_reason.value,(value => console.log(value)))

const {value: sub_reason_bringing_child, errorMessage: sub_reason_bringing_childError} = useField('sub_reason_bringing_child', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: arrived_reason_file, errorMessage: arrived_reason_fileError} = useField('arrived_reason_file', yup.mixed().nullable(), {
  initialValue: null,
});

const {value: arrived_date, errorMessage: arrived_dateError} = useField('arrived_date', yup.string().required(requiredValidatorMessage), {
  initialValue: '',
});

const {value: determined_location, errorMessage: determined_locationError} = useField('determined_location', yup.number().required(requiredValidatorMessage), {
  initialValue: undefined,
});
const dynamicValidationForForeignCountry = computed(() => {
  if (determined_location.value === FOREIGN_LOCATION_TYPE) {
    return yup.number().required(requiredValidatorMessage);
  } else {
    return yup.number().notRequired();
  }
});

const {value: foreign_country, errorMessage: foreign_countryError} = useField('foreign_country', dynamicValidationForForeignCountry);
const {value: prophylactic_inspector, errorMessage: prophylactic_inspectorError} = useField('prophylactic_inspector', yup.object().required(workerIsRequiredValidatorMessage).nullable(), {
  initialValue: null,
});

const {value: filled_date, errorMessage: filled_dateError} = useField('filled_date', yup.string(), {
  initialValue: new Date(),
});

watch(
  () => selectedJuvenile.value,
  (val) => {
    arrived_count.value = val.accepted_center_number;
    arrived_count_showed.value = val.accepted_center_number + 1;
  },
  {immediate: true}
);
</script>
<style src="../../node_modules/vue-multiselect/dist/vue-multiselect.css"></style>

<style>
.multiselect__tag {
  @apply !bg-primary;
}

.multiselect__tag-icon::after {
  @apply !text-white;
}

.multiselect__option:hover {
  @apply bg-primary;
}
</style>
