import { ref } from "vue";
import InfoService from "../../services/info.service";

export default function useSubBringingReason() {
  const subBringingReasonList = ref([]);
  const fetchSubBringingReasonList = async (parent) => {
    if (parent) {
      const {
        data: { sub_reason_bringing_child },
      } = await InfoService.fetchSubBringingReasonList(parent);
      subBringingReasonList.value = sub_reason_bringing_child;
    }
  };

  return {
    subBringingReasonList,
    fetchSubBringingReasonList,
  };
}
