import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useHaveBeenInRotmTypes() {
  const haveBeenInRotmTypes = ref([]);
  const fetchHaveBeenInRotmTypes = async () => {
    const { data } = await InfoService.fetchHaveBeenInRotmTypes();
    haveBeenInRotmTypes.value = data;
  };
  onMounted(async () => {
    await fetchHaveBeenInRotmTypes();
  });

  return {
    haveBeenInRotmTypes,
  };
}
